body {
  margin: 0;
  padding: 0;
  width:100%;
}
.Background{
background-color: #ececec;
display: flex;
height: 100vh;
justify-content: center;
}
.Frame{
  background-color: #fff;
  width:45%;
  height:60%;
  margin:auto;
  border-radius: 10px;
  padding:20px;
  text-align: center;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.content{
  display : block;
  height:100%;
}
input{
  width: 10%;
  height: 9%;
  border: 1px solid gray;
  font-size: 1rem;
  margin-left:5px;
  margin-right:10px;
  margin-top: 30px;
}
label{
  font-size: 1rem;
  margin-top: 30px;
}
.result{
width:60%;
height: 40%;
border: 1px solid black;
margin: auto;
margin-top: 2%;
overflow: auto;
display: block;
}
.inputs{
  display: block;
  padding:5px;
}
button{
  justify-content: center;
  margin-top: 10%;
  width: 20%;
  height: 10%;
  font-size: 1rem;
  background-color: #ececec;
}
button:hover{
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.h3{
  font-size: 1rem;
}
@media(max-width: 1023px){
  .Frame{
    width:60%;
    height:80%;
    display: block;
  }
  label{
    display: flex;
    justify-content: center;
    font-size: 0.8rem;
    margin-top: 1px;
    align-items: baseline
  }
  input{
    width: 20%;
    margin-left:10px;
    margin-top: 10px;
    font-size: 0.8rem;
  }
  button{
    width: 50%;
  }
  .result{
    width:80%;
    height: 50%;
    border: 1px solid black;
    margin: auto;
    margin-top: 10px;
    overflow: auto;
    display: block;
    }
}